.contactform{
	width: 40%;
	padding: 40px;
	background: #fff;}
.contactform h2{
	font-size: 30px;
	color: #333;
	font-weight: 500;
}

.contactform .input{
	position: relative;
	width: 100%;
	margin-top: 10px;
}

.contactform .input input,
.contactform .input textarea{
	width: 100%;
	padding: 5px 0;
	font-size: 25px;
	margin: 10px 0;
	border: none;
	border-bottom: 2px solid #333;
	outline: none;
	resize: none;
}

.contactform .input span {
	position: absolute;
	left: 0;
	padding: 5px 0;
	font-size: 25px;
	margin: 10px 0;
	pointer-events: none;
	transition: 0.5s;
	color: #666;

}
.contactform .input input:focus ~ span,
.contactform .input input:valid ~ span,
.contactform .input textarea:focus ~ span,
.contactform .input textarea:valid ~ span{
	color: #e91e63;
	font-size: 25px;
	-webkit-transform: translateY(-20px);
	        transform: translateY(-20px);



}
.contactform .input input[type = "submit"]
{background: #00bcd4;
width: 100%;
color: #fff;
border: none;
cursor: pointer;
padding :10px;
font-size: 18px;

}


